    import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

	import {mapGetters, mapState} from "vuex";

    export default {
        data() {
            return {
                list: [],
                iphone_x: false,
                tabList: [
                    {id:1, name: '未使用'},
                    {id:2, name: '已使用'},
                    {id:3, name: '已过期'}
                ],
                activeTab: 1,
                first: false,
                page: 2,
            }
        },
        components: {
            "app-tab-nav": appTabNav
        },
        computed: {
			...mapGetters('mallConfig', {
				getTheme: 'getTheme',
			}),
        },
        methods: {
        	tabStatus(e) {
                this.list = [];
                this.page = 2;
                this.activeTab = e.currentTarget.dataset.id;
                uni.showLoading({
                    title: '加载中...'
                });
                this.getList();
        	},

            getList() {
                let that = this;
                that.first = true;
                that.$request({
                    url: that.$api.coupon.user_coupon,
                    data: {
                    	status: that.activeTab
                    }
                }).then(response=>{
                    uni.hideLoading();
                    that.$hideLoading();
                    if(response.code == 0) {
                        that.list = response.data.list;
                        that.page = 2;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    that.$hideLoading();
                    that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                        that.getList();
                    });
                });
            },
            getMore() {
                let that = this;
                uni.showLoading({
                    title: '加载中...'
                });
                that.$request({
                    url: that.$api.coupon.user_coupon,
                    data: {
                    	status: that.activeTab,
                        page: that.page
                    }
                }).then(response=>{
                    uni.hideLoading();
                    if(response.code == 0) {
                        if(response.data.list.length > 0) {
                            that.list = that.list.concat(response.data.list);
                            that.page++;
                        }
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(e => {
                    uni.hideLoading();
                });
            },
            toDetail(id) {
                uni.navigateTo({
                    url: '/pages/coupon/details/details-no-share?person=1&id=' + id
                });
            },
            toList() {
                uni.navigateTo({
                    url: '/pages/coupon/list/list'
                });
            },
        },
        onLoad() { this.$commonLoad.onload();
            let that = this;
            uni.getSystemInfo({
                success: function (res) {
                    if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                        that.iphone_x = true;
                    }
                }
            })
            that.$showLoading({
                text: '加载中...'
            });
            that.getList();
        },

        onShow() {
            if(this.first) {
                this.getList();
            }
        },
        onReachBottom() {
            this.getMore();
        },
         filters: {
            reservedNum(data) {
                return Number(data);
            }
        }
    }
